import React, { useEffect, useState } from 'react'
import { Form, Field } from 'redux-form'
import { t } from 'i18next'
import { Input, Select, MultiSelect, SearchMap, PhoneInput } from 'Shared/components/FormElements'
import useApiGetCities from 'Shared/services/api/useApiGetCities'

function IntroductionSectionForm (props) {
  const { selectedCountry, initialValues } = props
  const selectedCity = [{ label: initialValues.city, value: initialValues.city }]
  const [listedCities, setListedCities] = useState('')
  const { apiGetCities } = useApiGetCities()

  useEffect(() => {
    props.fetchCountries()
  }, [])

  useEffect(() => {
    if (!selectedCountry) return
    setListedCities(selectedCity || [])
    apiGetCities(selectedCountry).then(res => {
      res && setListedCities(res)
    })
  }, [selectedCountry])

  return (
    <Form onSubmit={props.handleSubmit}>
      <div className='columns form__wrapper--inline'>
        <div className='column col-6 col-xs-12'>
          <Field
            component={Input}
            label={t('forms.firstName.label')}
            name='firstName'
            type='text'
            placeholder={t('forms.firstName.placeholder')}
            className='step_basic__input_small'
          />
        </div>
        <div className='column col-6 col-xs-12'>
          <Field
            component={Input}
            label={t('forms.lastName.label')}
            name='lastName'
            type='text'
            placeholder={t('forms.lastName.placeholder')}
            className='step_basic__input_small'
          />
        </div>
      </div>
      <div className='columns form__wrapper--inline form__wrapper--inline-input form__group--margin'>
        <div className='column col-sm-12'>
          <Field
            onSelect={props.resetCity}
            component={Select}
            label={t('forms.countryOfResidence.label')}
            options={props.countriesOptions}
            placeholder={t('forms.countryOfResidence.placeholder')}
            name='countryOfResidence'
            withIcon
          />
        </div>
        <div className='column col-sm-12'>
          <Field
            // component={SearchMap}
            component={Select}
            label={t('forms.city.label')}
            placeholder={t('forms.city.placeholder')}
            selectedCountry={props.selectedCountry}
            // suggestTypes={['(cities)']}
            options={listedCities}
            single='city'
            mapVisible={false}
            name='city'
          />
        </div>
      </div>
      <div className='columns form__wrapper--inline form__wrapper--inline-input form__group--margin'>
        <div className='column col-6'>
          <Field
            component={PhoneInput}
            className={'form-group'}
            label={t('forms.phoneNumber.label')}
            name='phoneNumber'
          />
        </div>
      </div>
    </Form>
  )
}

export default IntroductionSectionForm



