import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import withTranslations from 'Shared/hoc/withTranslations'
import cx from 'classnames'
import mixpanel from 'mixpanel-browser'

import './ShareButton.scss'

class ShareButton extends PureComponent {
  handleClick = () => {
    mixpanel.track('Button Click - Share - Jobs');

    if (this.props.onClick) {
      this.props.onClick();
    }
  };

  render() {
    const { withText, isActive } = this.props;
    const buttonClass = cx({
      'button share-button': true,
      'is_active': isActive,
      'with-text': withText,
      [this.props.className]: this.props.className,
    });

    return (
      <button className={buttonClass} type='button' disabled={false} onClick={this.handleClick}>
        <i className={this.props.iconClass} />
        {withText && this.props.t('common.share')}
      </button>
    );
  }
}

ShareButton.defaultProps = {
  onClick: () => {},
  iconClass: 'icon-im-replay-back',
};

ShareButton.propTypes = {
  onClick: PropTypes.func,
  buttonClass: PropTypes.string,
  isActive: PropTypes.bool,
  iconClass: PropTypes.string,
};

export default withTranslations(ShareButton);


